import type { ImageData } from '../../mo/models';

export const getZoomImageData = (
  data: ImageData,
  zoom: number,
): ImageData => {
  const { width, height, rectClip } = data!;
  const zoomRectClip = { ...rectClip };
  if (typeof zoomRectClip === 'object' && zoomRectClip) {
    const { top, right, bottom, left } = zoomRectClip;
    zoomRectClip.top = top ? top * zoom : top;
    zoomRectClip.right = right ? right * zoom : right;
    zoomRectClip.bottom = bottom ? bottom * zoom : bottom;
    zoomRectClip.left = left ? left * zoom : left;
  }
  return {
    ...data,
    width: width! * zoom,
    height: height! * zoom,
    rectClip: zoomRectClip,
  };
};
